import { onMounted, onBeforeUnmount } from 'vue'
import Swiper from 'swiper/js/swiper.min.js'
// Import Swiper styles
import 'swiper/css/swiper.min.css'

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
const swiperInit = function (viewMin = 2, viewMax = 3, len, hasPagination = false, classKey = "music-swiper") {
  console.log('init');
  //swiper
  let localClass = classKey + Number.parseInt(Math.random() * 1000000)
  let view = viewMin
  let navigation = {
    nextEl: `.${localClass}  .swiper-button-next`,
    prevEl: `.${localClass}  .swiper-button-prev`
  }
  const swiperSetting = {
    slidesPerView: view,
    slidesPerGroup: view,
    spaceBetween: 20,
    loop: true,
    speed: 300,
    navigation: navigation,
  }

  //pagination
  if (hasPagination) {
    swiperSetting.pagination = {
      el: '.swiper-pagination',
      clickable: true
    }
  }
  //swiper
  let swiper
  //swiper swiper
  function changeSet () {
    swiperSetting.slidesPerView = view
    swiperSetting.slidesPerGroup = view
    if (len > view) {
      document.querySelector(`.${localClass} .swiper-button-next`).style.display = 'block'
      document.querySelector(`.${localClass} .swiper-button-prev`).style.display = 'block'
    } else {
      document.querySelector(`.${localClass} .swiper-button-next`).style.display = 'none'
      document.querySelector(`.${localClass} .swiper-button-prev`).style.display = 'none'
    }
    if (swiper) {
      swiper.destroy()
      swiper = new Swiper(`.${localClass}  .swiper`, swiperSetting)
    } else {
      swiper = new Swiper(`.${localClass}  .swiper`, swiperSetting)

    }


  }



  // swiper changeSet
  function changeSize () {
    if (window.innerWidth < 1440) {
      if (view !== viewMin) {
        view = viewMin
        changeSet()
      }
    } else {
      if (view !== viewMax) {
        view = viewMax
        changeSet()
      }
    }
  }
  //
  onMounted(() => {
    view = window.innerWidth < 1440 ? viewMin : viewMax
    changeSet()
    window.addEventListener('resize', changeSize)
  })
  //
  onBeforeUnmount(() => {
    window.removeEventListener('resize', changeSize)
  })
  return {
    localClass
  }
}

export default swiperInit

