<template>
  <div class="title-msg">
    <div class="title">{{moduleMsg.title}}</div>
    <div class="sub-title">{{moduleMsg.subTitle}}
    </div>
    <div class="more"
         @click="go(moduleMsg.moreUrl)"
         v-if="moduleMsg.moreUrl"> ></div>
  </div>
  <div class="music-swiper-box"
       :class="localClass">
    <div class="swiper">
      <!-- Additional required wrapper -->
      <div class="swiper-wrapper">
        <!-- Slides -->
        <template v-for="msg,ind of moduleMsg.list"
                  :key="msg+ind">

          <div class="swiper-slide swiper-no-swiping ">
            <router-link :to="{name:'music',query:{ind:ind}}"
                         class="img-box m-link">
              <img :src="msg.imgUrl" />
              <div class="img-mask"></div>
              <div class="play">
                <only-play></only-play>
              </div>
              <div class="play-num"
                   v-if=" msg.playNum">
                <head-phone></head-phone>
                {{msg.playNum}}
              </div>
            </router-link>

            <div class="information-box">
              <div v-if="msg.recText"
                   class="info-text">{{msg.recText}}</div>
              <div v-if="msg.name"
                   class="info-name">{{msg.name}}</div>
              <div v-if="msg.author"
                   class="info-author">{{msg.author}}</div>
              <div v-if="msg.date"
                   class="info-date">{{msg.date}}</div>

            </div>
          </div>
        </template>

      </div>
    </div>
    <div class="swiper-button-prev "></div>
    <!--swiper-->
    <div class="swiper-button-next "></div>
    <!--swiper-->

  </div>
</template>
<script >
import swiperIn from '../../hooks/swiperIn'

import { defineComponent } from 'vue'
import OnlyPlay from '../icon/onlyPlay.vue'
import HeadPhone from '../icon/headPhone.vue'

export default defineComponent({
  props: {
    moduleMsg: {
      type: Object,
      require: true
    },
    minView: {
      type: Number,
      default: 3
    },
    maxView: {
      type: Number,
      default: 4
    }
  },
  setup (props) {
    const { localClass } = swiperIn(props.minView, props.maxView, props.moduleMsg.list.length)
    const go = function (val) {
      alert(val)
    }
    return {
      localClass,
      go,
      random: Math.random
    }
  },
  components: { OnlyPlay, HeadPhone }
})
</script >
<style lang="less" scoped>
@import '../../assets/style.less';

.title-msg {
  display: flex;
  align-items: center;
  width: 100%;
  .title {
    font-size: 25px;
    font-weight: 500;
  }
  .sub-title,
  .more {
    color: @m-gray;
    font-size: 15px;
  }
  .sub-title {
    margin-left: 20px;
  }
  .more {
    margin-left: auto;
    .hover-style();
  }
}
.music-swiper-box {
  display: block;
  position: relative;
  width: 100%;
  margin-bottom: 30px;
  &:hover {
    .swiper-button-next,
    .swiper-button-prev {
      opacity: 1;
    }
  }
  .swiper {
    position: relative;
    width: 100%;
    overflow: hidden;
    .swiper-wrapper {
      position: relative;
      padding-top: 30px;
      .swiper-slide {
        .img-box {
          position: relative;
          width: 100%;
          height: auto;
          border-radius: 10px;
          cursor: pointer;
          overflow: hidden;
          transition: all 0.3s ease;

          &:hover {
            transform: translateY(-20px);
            .img-mask {
              opacity: 0.5;
            }
            .play {
              opacity: 1;
            }
            .play-num {
              opacity: 0;
            }
          }
          img {
            position: absoult;
            border-radius: 10px;
            width: 100%;
            cursor: pointer;
          }
          .img-mask {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #000;
            opacity: 0;
          }
          .play {
            position: absolute;
            left: 50%;
            top: 50%;
            width: 50px;
            height: 50px;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            color: #000;
            background-color: #fff;
            opacity: 0;
            transition: all 0.3s ease;
            &:hover {
              background-color: @m-green;
            }
            svg {
              position: absolute;
              left: 50%;
              top: 50%;
              width: 30px;
              height: 30px;
              padding-left: 5px;
              transform: translate(-50%, -50%);
            }
          }
          .play-num {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 5px;
            position: absolute;
            right: 15px;
            bottom: 20px;
            padding: 5px 8px;
            height: 20px;
            color: #fff;
            font-size: 12px;
            border-radius: 25/2px;
            background-color: rgba(0, 0, 0, 0.8);
            transition: all 0.3s ease;
          }
        }
        .information-box {
          margin-top: 10px;
          font-size: 15px;
          .info-text,
          .info-name {
            .hover-style();
          }

          .info-author,
          .info-date {
            font-size: 10px;
            color: @m-gray;
            .hover-style();
            margin-top: 5px;
          }
        }
      }
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    opacity: 0;
    color: #cfcfcf;
    &:hover {
      opacity: 1;
      color: @m-green;
    }
  }
  .swiper-button-prev {
    left: -35px;
  }
  .swiper-button-next {
    right: -35px;
  }
  .swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: space-around;
    left: 50%;
    bottom: -20px;
    transform: translateX(-50%);
    width: 20%;
    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      cursor: pointer;
      &:hover {
        opacity: 1;
        background-color: #1ece9c;
      }
    }
    .swiper-pagination-bullet-active {
      background-color: #313131;
    }
  }
}
</style>
