import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'
import MusicView from '../views/musicView.vue'
import N from '../components/n.vue'
import SelectedView from '../views/music/selectedView.vue'


const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    redirect: { name: 'music' }
  },
  {
    path: '/music',
    name: 'music',
    component: MusicView,
    redirect: { name: 'selected' },
    children: [{
      path: 'selected',
      name: 'selected',
      component: SelectedView
    }, {
      path: 'audiostation',
      name: 'audiostation',
      component: N
    }, {
      path: 'ranking',
      name: 'ranking',
      component: N
    }, {
      path: 'singer',
      name: 'singer',
      component: N
    }, {
      path: 'songsheet',
      name: 'songsheet',
      component: N
    }, {
      path: 'digitalalbum',
      name: 'digitalalbum',
      component: N
    }, {
      path: 'phone',
      name: 'phone',
      component: N
    },]
  },
  {
    path: '/video',
    name: 'video',
    component: N,
  },
  {
    path: '/radio',
    name: 'radio',
    component: N,
  },
  {
    path: '/local',
    name: 'local',
    component: N
  },
  {
    path: '/played',
    name: 'played',
    component: N
  }, {
    path: '/list',
    name: 'list',
    component: N
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})


router.beforeEach(() => {

  return true
})

export default router
