import { ListMsg, RouteMsg } from "@/types/Msg"

export function selectRouteMsgWapper(listMsgs: RouteMsg[]): RouteMsg[] {

  listMsgs.map((element) => {
    element.selected = false
  })
  return listMsgs
}

export function listMsgWapper(listMsgs: ListMsg[]): ListMsg[] {

  listMsgs = listMsgs.map((listMsg) => {
    let routeMsgs: RouteMsg[] = listMsg.list
    routeMsgs = selectRouteMsgWapper(routeMsgs)
    listMsg.list = routeMsgs
    return listMsg
  })
  return listMsgs
}
