<template>
  <div class="recommend-swiper-box"
       :class="localClass">
    <div class="swiper">
      <!-- Additional required wrapper -->
      <div class="swiper-wrapper">
        <!-- Slides -->
        <div class="swiper-slide "
             v-for="i of 5"
             :key="i+'rec'">

          <img src="../../assets/img/1.png"
               alt="" />

        </div>
        <div class="swiper-slide "
             v-for="i of 3"
             :key="i+'rec1'">

          <img src="../../assets/img/5.png"
               alt="" />
        </div>
        <div class="swiper-slide "
             v-for="i of 2"
             :key="i+'rec1'">

          <img src="../../assets/img/1.png"
               alt="" />
        </div>
      </div>
    </div>
    <div class="swiper-button-prev "></div>
    <!--swiper-->
    <div class="swiper-button-next "></div>
    <!--swiper-->
    <div class="swiper-pagination"></div>
    <!--swiper-->
  </div>
</template>
<script >


import { defineComponent } from 'vue'
import swiperIn from '../../hooks/swiperIn'
export default defineComponent({
  setup () {
    const { localClass } = swiperIn(2, 3, 10, true)
    return {
      localClass
    }
  },
  components: {}
})
</script>
<style lang="less" >
.recommend-swiper-box {
  margin-top: 20px;
  position: relative;
  width: 100%;
  margin-bottom: 40px;
  .swiper {
    position: relative;
    width: 100%;
    overflow: hidden;
    .swiper-wrapper {
      .swiper-slide {
        overflow: hidden;
        img {
          width: 100%;
          border-radius: 10px;
          cursor: pointer;
        }
      }
    }
  }

  &:hover {
    .swiper-button-next,
    .swiper-button-prev {
      opacity: 1;
    }
  }
  .swiper-button-next,
  .swiper-button-prev {
    opacity: 0;
