    &:hover {
      opacity: 1;
      color: #1ece99;
    }
  }
  .swiper-button-prev {
    left: -35px;
  }
  .swiper-button-next {
    right: -35px;
  }
  .swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: space-around;
    left: 50%;
    bottom: -20px;
    transform: translateX(-50%);
    width: 20%;
    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      cursor: pointer;
      &:hover {
        opacity: 1;
        background-color: #1ece9c;
      }
    }
    .swiper-pagination-bullet-active {
