
import { defineComponent, watch, inject, reactive } from 'vue'

import { ListMsg, RouteMsg } from '../types/Msg'
import { listMsgWapper } from '../hooks/wapper'
import { RouteLocationMatched, useRoute, useRouter } from 'vue-router'

export default defineComponent({
  setup() {
    const route = useRoute()
    const router = useRouter()
    const indListMsg = inject('indListMsg') as ListMsg[]

    const selfMsgs = reactive(listMsgWapper(indListMsg))
    const selected = function (routeMsg: RouteMsg): void {
      selfMsgs.forEach((element) => {
        element.list.forEach((routeMsg) => {
          routeMsg.selected = false
        })
      })
      routeMsg.selected = true
    }
    const selectedByName = function (routeMatched: RouteLocationMatched): void {
      if (routeMatched === undefined) {
        return
      }
      const routeName = routeMatched.name
      selfMsgs.forEach((element) => {
        element.list.forEach((routeMsg) => {
          if (routeMsg.name === routeName) {
            routeMsg.selected = true
          } else {
            routeMsg.selected = false
          }
        })
      })
    }

    watch(
      () => route.name,
      () => {
        selectedByName(route.matched[0])
      },
      { immediate: true }
    )

    return {
      selfMsgs,
      selected,
      router
    }
  }
})
