
import { defineComponent, PropType, watch, reactive } from 'vue'
import { RouteMsg } from '@/types/Msg'
import { RouteLocationMatched, useRoute, useRouter } from 'vue-router'

export default defineComponent({
  name: 'NavList',
  props: {
    listMsg: {
      type: Array as PropType<RouteMsg[]>,
      require: true,
      default: [
        { name: 'selected', title: '' },
        { name: 'audiostation', title: '' },
        { name: 'ranking', title: '' },
        { name: 'singer', title: '' },
        { name: 'songsheet', title: '' },
        { name: 'digitalalbum', title: '' },
        { name: 'phone', title: '' }
      ] as RouteMsg[]
    }
  },
  setup(props) {
    const router = useRouter()
    const route = useRoute()
    const selectedArr = reactive(new Array(props.listMsg.length).fill(false))

    const selectedByName = function (routeMatched: RouteLocationMatched): void {
      if (routeMatched === undefined) {
        return
      }
      const routeName = routeMatched.name
      props.listMsg.forEach((element, ind) => {
        if (element.name === routeName) {
          selectedArr[ind] = true
        } else {
          selectedArr[ind] = false
        }
      })
    }
    watch(
      () => route.name,
      () => {
        selectedByName(route.matched[1])
      },
      { immediate: true }
    )
    return {
      router,
      selectedArr
    }
  },

  components: {}
})
