import { RouterMsgState } from '../types/State'
export default {
  namespace: true,
  state: {
    routerNameArr: [],
    currentIndex: -1
  },
  mutations: {
    push(state: RouterMsgState, name: string): void {
      const length = state.routerNameArr.length
      const index = state.currentIndex
      if (!(index === -1)) {
        state.routerNameArr.splice(index + 1, length - 1 - index, name)
        state.currentIndex = state.routerNameArr.length - 1
      } else {
        state.routerNameArr.push(name)
        state.currentIndex = 0
      }
    },
    forward(state: RouterMsgState): void {
      if (state.currentIndex !== state.routerNameArr.length - 1 && state.currentIndex !== -1) {
        state.currentIndex++
      }
    },
    back(state: RouterMsgState): void {
      if (state.currentIndex !== 0 && state.currentIndex !== -1) {
        state.currentIndex--
      }

    }

  },
  actions: {},
  getters: {
    canBack: (state: RouterMsgState): boolean => {
      if (state.currentIndex > 0) {
        return true
      }
      return false
    },
    canForward: (state: RouterMsgState): boolean => {
      if (state.currentIndex < state.routerNameArr.length - 1 && state.currentIndex !== -1) {
        return true
      }
      return false
    },
    currentRouteName: (state: RouterMsgState): string => {
      return state.routerNameArr[state.currentIndex]
    }
  }
}