
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'IndContent',
  components: {},
  setup() {
    const fontRef = ref()
    let size = 35
    const height = 60

    const changeFont = function (e: Event) {
      const top = (e.target as Element).scrollTop
      if (height - top < 0) {
        size = 0
      } else {
        size = (35 * (height - top)) / height
      }
      fontRef.value.style.fontSize = size + 'px'
      fontRef.value.style.opacity = size / 35 + ''
    }
    return {
      changeFont,
      fontRef
    }
  }
})
